import { ComponentPropsWithRef, useState } from "react";
import { SegmentedButton, type SegmentedButtonProps } from "./SegmentedButton";
import styled, { css } from "styled-components";
import { fixedForwardRef } from "../../utils/component";

export type SegmentedButtonGroupOption = {
  properties: Omit<ComponentPropsWithRef<typeof SegmentedButton>, "$active">;
  key: string;
};

/** @design https://www.figma.com/design/JGFHYME2p7RpeMYcoMqooS/ODE-Library-1.0?node-id=11429-14810&t=yPRe6bLPZnxitmAJ-4 */
export interface SegmentedButtonGroupProps<
  TOption extends SegmentedButtonGroupOption
> extends ComponentPropsWithRef<"div"> {
  /** @description option.key | option.key array */
  active?: string;
  onActiveChange?: (
    activeKey: TOption["key"],
    event: React.MouseEvent<HTMLButtonElement>
  ) => void;
  /** @description button array */
  option: TOption[];
  /**
   * @default medium
   */
  $size?: SegmentedButtonProps["$size"];
  /**
   * @default false
   * @description override single button disable;
   */
  disabled?: boolean;
  /**
   * @description if fixedForwardRef & ref not work, try _ref
   * @todo should fix when react19 release
   */
  _ref?: React.ForwardedRef<HTMLDivElement>;
}

/**
 * @note for generic type usage, if need to use ref, please import fixedForwardRef from utils/components and `const SegmentedButtonGroup = fixedForwardRef(RawSegmentedButtonGroup);`
 * @design https://www.figma.com/design/JGFHYME2p7RpeMYcoMqooS/ODE-Library-1.0?node-id=6011-9842&t=FAzx6jwix7ISvVDA-4
 * @description radio (single) active
 */
export const SegmentedButtonGroup = <
  TOption extends SegmentedButtonGroupOption
>(
  props: SegmentedButtonGroupProps<TOption>,
  ref: React.ForwardedRef<HTMLDivElement>
) => {
  const {
    option,
    $size = "medium",
    active,
    disabled = false,
    onActiveChange,
    _ref,
    ...containerProps
  } = props;
  const [innerActive, setActive] = useState(() => {
    if (typeof active === "string") return active;
    return "";
  });
  const _active = typeof active === "string" ? active : innerActive;
  return (
    <Container
      {...containerProps}
      $size={$size}
      disabled={disabled}
      ref={(element) => {
        if (_ref) {
          if (typeof _ref === "function") {
            _ref(element);
            return;
          }
          if ("current" in _ref) _ref.current = element;
        }
        if (ref) {
          if (typeof ref === "function") {
            ref(element);
            return;
          }
          if ("current" in ref) ref.current = element;
        }
      }}
    >
      {option.map((item) => {
        const isActive = _active === item.key;
        const overrideProperties: Partial<
          ComponentPropsWithRef<typeof SegmentedButton>
        > = { $size };
        /** override disabled, control by parent */
        if (disabled) overrideProperties.disabled = true;
        if (disabled && !isActive) {
          overrideProperties.style = {
            ...(item.properties?.style || {}),
            backgroundColor: "transparent",
          };
        }
        return (
          <SegmentedButton
            {...item.properties}
            {...overrideProperties}
            $active={isActive}
            onClick={(event) => {
              if (disabled) return;
              if (_active === item.key) return;
              item.properties?.onClick?.(event);
              onActiveChange?.(item.key, event);
              setActive(item.key);
            }}
            key={item.key}
          />
        );
      })}
    </Container>
  );
};

export const SegmentedButtonGroupWithRef =
  fixedForwardRef(SegmentedButtonGroup);

type ContainerProps = Required<
  Pick<
    SegmentedButtonGroupProps<SegmentedButtonGroupOption>,
    "$size" | "disabled"
  >
>;

const Container = styled.div<ContainerProps>`
  ${(p) => p.theme.flex.row}
  width: fit-content;
  overflow: hidden;
  background-color: #f8fafc;
  transition: background-color 0.2s ease;
  padding: 2px;
  border-radius: ${(p) => p.theme.radius.xxxs}px;

  ${({ disabled }) => {
    if (disabled) {
      return css`
        background-color: ${(p) => p.theme.colors.button.disabled};
      `;
    }
    return undefined;
  }}
`;
